.additionally {
  position: relative;
}

.additionally::after {
  position: absolute;
  content: "";
  top: 16px;
  left: calc(100vw - 40%);
  right: 0;
  height: 2px;
  background-color: #68b738;
}

.additionally__filter-list {
  list-style-type: none;
  margin-bottom: 15px;
  text-transform: uppercase;
}

@media (min-width: @tablet-width) {
  .additionally__filter-list {
    min-width: 220px;
    margin-right: 40px;
  }
}

@media (min-width: @desktop-width) {
  .additionally__filter-list {
    display: flex;
    justify-content: space-between;
  }
}

@media (min-width: @tablet-width) {
  .additionally__filter {
    display: flex;
    flex-wrap: wrap;
  }
}
